@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin: 25px 10px;
}
footer p {
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: #272a31;
}
footer img {
  filter: grayscale(0.9);
  width: 160px;
}

.hero {
  background: url("../img/bg.jpg");
}

header {
  position: fixed;
  background: url("../img/bg.jpg");
  width: 100%;
  top: 0;
  padding: 25px;
  z-index: 10;
}
header nav svg {
  height: 25px;
  width: 25px;
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

hr {
  margin-top: 24px;
  border: 1px solid #ec5242;
  width: 50%;
  margin-inline: auto;
}

body {
  width: 100%;
}

ul,
li {
  padding: 0;
  list-style: none;
}

@font-face {
  font-family: "COCOGOOSE";
  src: url("../../../dist/assets/fonts/Cocogoose_trial.eot") format("eot"), url("../../../dist/assets/fonts/Cocogoose_trial.woff") format("woff"), url("../../../dist/assets/fonts/Cocogoose_trial.ttf") format("truetype"), url("../../../dist/assets/fonts/Cocogoose_trial.svg#COCOGOOSE") format("svg");
  font-weight: normal;
  font-style: normal;
}
.headline {
  padding: 80px 25px;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #ec5242;
}
.headline p {
  font-size: 16px;
}
.headline h1 {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  background-clip: content-box;
  background-image: url("../img/bg-txt.jpg");
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.headline .headline-box {
  background-color: #f7f7f7;
  border: 1px solid white;
  padding: 20px;
}
.headline .headline-box p {
  color: #272a31;
  font-size: 14px;
  line-height: 20px;
}
.headline .date-event {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  color: #272a31;
  margin: 25px 0;
}

.partners-logo {
  background-color: #272a31;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  padding: 50px 25px;
  text-align: center;
  color: white;
}
.partners-logo .wrap-logos ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  flex-wrap: wrap;
}
.partners-logo img {
  width: 90px;
  margin: 20px 10px;
  filter: grayscale(1);
}

.course {
  background: rgba(0, 0, 0, 0.7);
  background-image: url("../img/bg-courset.png");
  background-repeat: repeat;
  padding: 50px 20px;
}
.course h2 {
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  color: white;
}
.course ul {
  margin: 25px 0 25px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.course ul li {
  padding: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.3);
  gap: 12px;
}
.course ul li h3 {
  color: #ec5242;
}
.course ul li p {
  color: white;
}
.course ul li img {
  width: 38px;
  height: 38px;
  flex-basis: 30%;
}

.speakers {
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  color: #272a31;
  padding: 50px 20px 50px 20px;
}
.speakers h2 {
  text-align: center;
  font-weight: 500;
}
.speakers h3 {
  font-weight: 700;
  font-size: 16px;
}
.speakers ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.speakers ul li {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: 25px;
  padding: 10px;
}
.speakers ul li img {
  width: 100px;
  height: 100px;
}
.speakers ul li .bg-square {
  position: relative;
}
.speakers ul li .bg-square::after {
  content: url("../img/square-bg.png");
  position: absolute;
  z-index: -1;
  bottom: 12px;
  left: -14px;
  width: 90%;
  height: 90%;
  background: linear-gradient(red, firebrick);
}
.speakers ul li .description p {
  font-size: 12px;
  margin: 0;
  line-height: 15px;
}
.speakers ul li .description hr {
  width: 10%;
  margin-inline: 0;
  margin: 0;
  color: #d3d3d3;
}
.speakers ul li .description p:first-of-type {
  color: #ec5242;
  font-size: 12px;
  margin: 5px 0;
  font-style: italic;
}